.jumbotron {
  width: 100%;
  font-size: 2rem;
  color: rgb(0, 217, 255);
  background-color: rgb(56, 56, 56);
  height: auto;
  text-align: center;
  position: -webkit-sticky;
  position: sticky;

}
  
@media only screen and (max-width: 750px) {
  #cur-sco, #top-sco {
    font-size: 2rem;
  }
}


